/* Default Background */
.weather-container {
  text-align: center;
  margin-top: 50px;
  transition: background-image 0.5s ease-in-out;
  height: 100vh;
  background-size: cover;
  background-position: center;
}

.weather-container.clear-sky {
  background-image: url('https://media.istockphoto.com/id/1208368568/photo/dramatic-sunset-and-sunrise-sky-nature-background-with-white-clouds.jpg?s=612x612&w=0&k=20&c=mu12e5RAWbRKShGqpuRtNJHN7ZXHiohsmSBFbUXchT4='); /* Sunny background */
}

.weather-container.rainy {
  background-image: url('https://t4.ftcdn.net/jpg/10/16/05/75/360_F_1016057558_HcGf68Nh45qUy0D6f09vOBg9YiiETEbN.jpg'); /* Rainy background */
}

.weather-container.cloudy {
  background-image: url('https://media.istockphoto.com/id/1503488794/photo/panorama-of-beautiful-clouds-background-of-a-grey-sky-and-cloud.jpg?s=612x612&w=0&k=20&c=i3HqzyrHkYHWgnQO6UBWRh8j0aSN_azVu5vaBhYZcuM='); /* Cloudy background */
}

.weather-container.snowy {
  background-image: url('https://static.vecteezy.com/system/resources/thumbnails/030/726/586/small_2x/christmas-background-snowflake-on-snow-with-blurry-bokeh-light-on-red-background-horizontal-xmas-christmas-poster-greeting-cards-headers-website-generative-ai-photo.jpg'); /* Snowy background */
}

.weather-container.default {
  background-color: #f0f0f0; /* Default background */
}

/* Styles for weather info */
input[type='text'] {
  padding: 10px;
  font-size: 16px;
  width: 300px;
  margin-right: 10px;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
}

.error {
  color: red;
  font-weight: bold;
}

.weather-info {
  margin-top: 20px;
  color: white;
}

.weather-info img {
  margin-top: 10px;
  width: 100px;
}
