.App {
  text-align: center;
  padding: 20px;
}

input {
  padding: 8px;
  margin-right: 10px;
}

button {
  padding: 8px 16px;
  cursor: pointer;
}

.weather-details {
  margin-top: 20px;
  font-size: 18px;
}

.weather-details img {
  width: 50px;
  height: 50px;
}

.forecast {
  margin-top: 20px;
}

.forecast-item {
  margin-bottom: 10px;
}

.error {
  color: red;
  font-weight: bold;
}
